export default {
	data () {
		return {
			formData: {
				mobile: '',
				name: '',
				certType: '-1',
				certNo: '',
				code: ''
			},
			/* 
				01——身份证
				02——军人证
				03——护照
				04——港澳台通行证
				05——其他
			*/
			certTypes: [
				{
				 id: '01',
				 name: '身份证'
				},
				{
				 id: '02',
				 name: '军人证'
				},
				{
				 id: '03',
				 name: '护照'
				},
				{
				 id: '04',
				 name: '港澳台通行证'
				},
				{
				 id: '05',
				 name: '其他'
				}
			],
			// 验证码文字
			codeText: '发送验证码',
			// 获取验证码倒计时
			codeCountDown: 0
		}
	},
	methods: {
		// 获取验证码
		getCode () {
			if (!/^1[3456789]\d{9}$/.test(this.formData.mobile)) {
				this.$JFn.showError('手机格式错误')
				return
			}
			this.codeCountDown = 60
			this.codeText = '剩余' + this.codeCountDown + 's'
			let timer = setInterval(() => {
				this.codeText = '剩余' + (--this.codeCountDown) + 's'
				if (this.codeCountDown === 0) {
					this.codeText = '发送验证码'
					clearInterval(timer)
				}
			}, 1000)
			// 发送验证码
			this.$JRequest('sendCode', { mobile: this.formData.mobile }, () => {
				this.$JFn.showSuccess('发送验证码成功')
			})
		},
		enter () {
			this.$JRequest('regi', { ...this.formData, openId: uni.getStorageSync('openid') }, () => {
				this.$JFn.showSuccess('登记成功')
				uni.removeStorageSync('openid')
				this.$JFn.showModal({
					content: '是否继续完善信息？',
					cancelText: '跳过',
					confirmText: '继续',
					success: res => {
						if (res.confirm) {
							uni.reLaunch({
								url: '/pages/my/certification/index'
							})
						} else if (res.cancel) {
							uni.reLaunch({
								url: '/pages/index/index'
							})
						}
					}
				})
			})
		}
	}
}
